/* Navbar Styles */
.navbar {
    background: #1a1a1a;
    border-bottom: 1px solid #333;
    position: sticky;
    top: 0;
    z-index: 1000;
    border-radius: 12px 12px 0 0;
}

.navbar-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 1.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 70px;
}

/* Brand Logo */
.navbar-brand a {
    color: white;
    text-decoration: none;
    font-size: 1.5rem;
    font-weight: 700;
    letter-spacing: -0.02em;
}

/* Desktop Navigation Links */
.navbar-links {
    display: flex;
    align-items: center;
    gap: 2rem;
}

.nav-link {
    color: white;
    text-decoration: none;
    font-weight: 500;
    font-size: 0.95rem;
    transition: color 0.2s ease;
    display: flex;
    align-items: center;
    gap: 0.25rem;
}

.nav-link:hover {
    color: #00d4ff;
}

.dropdown-toggle {
    cursor: pointer;
}

.dropdown-icon {
    transition: transform 0.2s ease;
}

.nav-dropdown:hover .dropdown-icon {
    transform: rotate(180deg);
}

/* Desktop Action Buttons */
.navbar-actions {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.btn-login {
    background: transparent;
    border: 1px solid #666;
    color: white;
    padding: 0.75rem 1.5rem;
    border-radius: 8px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;
    font-size: 0.9rem;
}

.btn-login:hover {
    border-color: #00d4ff;
    color: #00d4ff;
}

.btn-explore {
    background: linear-gradient(135deg, #00d4ff 0%, #00a8cc 100%);
    border: none;
    color: white;
    padding: 0.75rem 1.5rem;
    border-radius: 8px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s ease;
    font-size: 0.9rem;
    box-shadow: 0 2px 8px rgba(0, 212, 255, 0.3);
}

.btn-explore:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(0, 212, 255, 0.4);
}

/* Mobile Menu Toggle */
.mobile-menu-toggle {
    display: none;
    flex-direction: column;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0.5rem;
    gap: 4px;
}

.hamburger-line {
    width: 24px;
    height: 2px;
    background: white;
    transition: all 0.3s ease;
    border-radius: 1px;
}

.mobile-menu-toggle.active .hamburger-line:nth-child(1) {
    transform: rotate(45deg) translate(5px, 5px);
}

.mobile-menu-toggle.active .hamburger-line:nth-child(2) {
    opacity: 0;
}

.mobile-menu-toggle.active .hamburger-line:nth-child(3) {
    transform: rotate(-45deg) translate(7px, -6px);
}

/* Mobile Menu */
.mobile-menu {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background: #1a1a1a;
    border-top: 1px solid #333;
    transform: translateY(-100%);
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
}

.mobile-menu.active {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
}

.mobile-menu-content {
    padding: 1.5rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.mobile-nav-link {
    color: white;
    text-decoration: none;
    font-weight: 500;
    padding: 0.75rem 0;
    border-bottom: 1px solid #333;
    transition: color 0.2s ease;
}

.mobile-nav-link:hover {
    color: #00d4ff;
}

.mobile-menu-actions {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
    margin-top: 1rem;
    padding-top: 1rem;
    border-top: 1px solid #333;
}

.btn-login-mobile {
    background: transparent;
    border: 1px solid #666;
    color: white;
    padding: 0.75rem 1.5rem;
    border-radius: 8px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;
    width: 100%;
}

.btn-login-mobile:hover {
    border-color: #00d4ff;
    color: #00d4ff;
}

.btn-explore-mobile {
    background: linear-gradient(135deg, #00d4ff 0%, #00a8cc 100%);
    border: none;
    color: white;
    padding: 0.75rem 1.5rem;
    border-radius: 8px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s ease;
    width: 100%;
    box-shadow: 0 2px 8px rgba(0, 212, 255, 0.3);
}

.btn-explore-mobile:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(0, 212, 255, 0.4);
}

/* Responsive Design */
@media (max-width: 768px) {
    .navbar-links,
    .navbar-actions {
        display: none;
    }
    
    .mobile-menu-toggle {
        display: flex;
    }
    
    .mobile-menu {
        display: block;
    }
    
    .navbar-container {
        padding: 0 1rem;
    }
}

@media (max-width: 480px) {
    .navbar-container {
        padding: 0 0.75rem;
    }
    
    .navbar-brand a {
        font-size: 1.25rem;
    }
}

